import React, { useState, useEffect } from 'react';
import { AppBar, Typography, Toolbar, Avatar, Button } from '@material-ui/core';
import { Link, useHistory, useLocation } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import decode from 'jwt-decode';
import FontAwesome from 'react-fontawesome'
import 'font-awesome/css/font-awesome.min.css';
import memoriesLogo from '../../images/memoriesLogo.png';
import booklogo from '../../images/book.jpg'
import bookgif from '../../images/BuyBooKS.gif'
import memoriesText from '../../images/memoriesText.png';
import * as actionType from '../../constants/actionTypes';
import useStyles from './styles';
import {getLatestMessages } from 'react-chat-engine';

const Navbar = () => {
  const [user, setUser] = useState(JSON.parse(localStorage.getItem('profile')));
  const dispatch = useDispatch();
  const location = useLocation();
  const history = useHistory();
  const classes = useStyles();

  const logout = () => {
    dispatch({ type: actionType.LOGOUT });

    history.push('/auth');

    setUser(null);
  };

  const chats = () => {


    history.push(`/chats`)




  }

  useEffect(() => {
    const token = user?.token;

    if (token) {
      const decodedToken = decode(token);

      if (decodedToken.exp * 1000 < new Date().getTime()) logout();
    }

    setUser(JSON.parse(localStorage.getItem('profile')));
  }, [location]);

  return (
    <AppBar className={classes.appBar} position="static" color="inherit">
      <Link to="/" className={classes.brandContainer}>
        {/* <img component={Link} to="/" src={memoriesText} alt="icon" height="45px" /> */}
        <img src={bookgif} className={classes.heading}/>
        <img className={classes.image} src={booklogo} alt="icon" />
        
      </Link>
      
      <Toolbar className={classes.toolbar}>
        {user?.result ? (
          <div className={classes.profile}>
            <Avatar className={classes.purple} alt={user?.result.name} src={user?.result.imageUrl}>{user?.result.name.charAt(0)}</Avatar>
            <Typography className={classes.userName} variant="h6">{user?.result.name}</Typography>
            <Link  style={{color:'#52ab98',textDecoration: 'none',}} onClick={chats}><Typography variant="h6">Chats</Typography></Link>
            <Button variant="contained" className={classes.logout} color="secondary" onClick={logout}>Logout</Button>
          </div>
        ) : (
          <Button style={{backgroundColor:'#52ab98'}} component={Link} to="/auth" variant="contained" color="primary">Sign In</Button>
        )}
      </Toolbar>
     
    </AppBar>
  );
};

export default Navbar;
